// ** React Imports
import { Link } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Third Party Components
import classnames from 'classnames'
import { ReactSortable } from 'react-sortablejs'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { Menu, Search, MoreVertical } from 'react-feather'
import { orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()
// ** Reactstrap Imports
import {
  Input,
  Badge,
  InputGroup,
  InputGroupText,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
  UncontrolledDropdown
} from 'reactstrap'
import moment from 'moment'

const Tasks = props => {
  // ** Props
  const {
    query,
    tasks,
    params,
    dispatch,
    getTasks,
    setSort,
    setQuery,
    updateTask,
    selectTask,
    reOrderTasks,
    handleTaskSidebar,
    handleMainSidebar
  } = props

  // ** Function to selectTask on click
  const handleTaskClick = obj => {
    dispatch(selectTask(obj))
    handleTaskSidebar()
  }

  const tagOptions = {
    team: 'Team',
    self: 'Self'
  }

  // ** Renders task tags
  const colors = ['light-primary', 'light-success', 'light-warning']
  const renderTags = tag => {
    const badgeColor = {
      team: 'light-primary',
      self: 'light-success'
    }

    return (
      <Badge className='text-capitalize' color={badgeColor[tag]} pill>
        {tagOptions[tag]}
      </Badge>
    )
  }

  const taskCompleted = (flag, item) => {
    const data = { ...item }
    data.updatedBy = userId
    data.status = flag ? 2 : 1
    data.assignee = data.assigneeid
    dispatch(updateTask({ ...data, isCompleted: flag }))
  }

  // ** Renders Avatar
  const renderAvatar = (username, k) => {
    return <Avatar key={k} color={colors[k]} content={username || 'T'} initials />
  }

  const renderTasks = () => {
    return (
      <PerfectScrollbar
        className='list-group todo-task-list-wrapper'
        options={{ wheelPropagation: false }}
        containerRef={ref => {
          if (ref) {
            ref._getBoundingClientRect = ref.getBoundingClientRect

            ref.getBoundingClientRect = () => {
              const original = ref._getBoundingClientRect()

              return { ...original, height: Math.floor(original.height) }
            }
          }
        }}
      >
        {tasks.length ? (
          <ReactSortable
            tag='ul'
            list={tasks}
            handle='.drag-icon'
            className='todo-task-list media-list'
            setList={newState => dispatch(reOrderTasks(newState))}
          >
            {tasks.map(item => {
              return (
                <li
                  key={item.id}
                  onClick={() => handleTaskClick(item)}
                  className={classnames('todo-item', {
                    completed: item.isCompleted
                  })}
                >
                  <div className='todo-title-wrapper'>
                    <div className='todo-title-area col-md-9'>
                      <MoreVertical className='drag-icon' />
                      <div className='form-check'>
                        <Input
                          type='checkbox'
                          id={item.title}
                          checked={parseInt(item.status) === 2}
                          onClick={e => e.stopPropagation()}
                          onChange={e => {
                            e.stopPropagation()
                            taskCompleted(e.target.checked, item)
                          }}
                        />
                      </div>
                      <span className={`todo-title ${parseInt(item.status) === 2 ? 'text-muted' : ''}`}>{item.title}</span>
                    </div>
                    <div className='todo-item-action mt-lg-0 mt-50 col-md-2 justify-content-end'>
                      {item.assigneelist.map((name, k) => { return (renderAvatar(name, k)) })}
                      {<div className='badge-wrapper ms-50'>{renderTags(item.tags)}</div>}
                      <span className='text-nowrap font-small-3 text-muted ms-50'>
                        {moment(item.dueDate, 'x').format('MMM DD')}
                      </span>
                    </div>
                  </div>
                </li>
              )
            })}
          </ReactSortable>
        ) : (
          <div className='no-results show'>
            <h5>No Items Found</h5>
          </div>
        )}
      </PerfectScrollbar>
    )
  }

  // ** Function to getTasks based on search query
  const handleFilter = e => {
    setQuery(e.target.value)
    dispatch(getTasks({ ...params, q: e.target.value }))
  }

  // // ** Function to getTasks based on sort
  const handleSort = (e, val) => {
    e.preventDefault()
    setSort(val)
    dispatch(getTasks({ ...params, sort: val }))
  }

  return (
    <div className='todo-app-list'>
      <div className='app-fixed-search d-flex align-items-center'>
        <div className='sidebar-toggle cursor-pointer d-block d-lg-none ms-1' onClick={handleMainSidebar}>
          <Menu size={21} />
        </div>
        <div className='d-flex align-content-center justify-content-between w-100'>
          <InputGroup className='input-group-merge'>
            <InputGroupText>
              <Search className='text-muted' size={14} />
            </InputGroupText>
            <Input placeholder='Search task' value={query} onChange={handleFilter} />
          </InputGroup>
        </div>
        <UncontrolledDropdown>
          <DropdownToggle className='hide-arrow mr-1' tag='a' href='/' onClick={e => e.preventDefault()}>
            <MoreVertical className='text-body' size={16} />
          </DropdownToggle>
          <DropdownMenu end>
            <DropdownItem tag={Link} to='/' onClick={e => handleSort(e, 'title ASC')}>
              Sort A-Z
            </DropdownItem>
            <DropdownItem tag={Link} to='/' onClick={e => handleSort(e, 'title DESC')}>
              Sort Z-A
            </DropdownItem>
            <DropdownItem tag={Link} to='/' onClick={e => handleSort(e, 'reminderdate DESC')}>
              Sort Due Date
            </DropdownItem>
            <DropdownItem tag={Link} to='/' onClick={e => handleSort(e, '')}>
              Reset Sort
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
      </div>
      {renderTasks()}
    </div>
  )
}

export default Tasks
