// ** React Imports
import { useState, Fragment, useEffect } from 'react'

// ** Third Party Components
import classnames from 'classnames'
import Flatpickr from 'react-flatpickr'
import { Editor } from 'react-draft-wysiwyg'
import { X, Star, Trash } from 'react-feather'
import Select, { components } from 'react-select' //eslint-disable-line
import { useForm, Controller } from 'react-hook-form'
import { EditorState, ContentState, convertToRaw, convertFromHTML } from 'draft-js'
import draftToHtml from 'draftjs-to-html'
import Avatar from '@components/avatar'
// ** Reactstrap Imports
import { Modal, ModalBody, Button, Form, Input, Label, FormFeedback } from 'reactstrap'
import { updateStatus } from './store'
// ** Utils
import { isObjEmpty, selectThemeColors } from '@utils'
import axios from '@src/configs/axios/axiosConfig'
// ** Styles Imports
import '@styles/react/libs/editor/editor.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/react/libs/react-select/_react-select.scss'
import moment from 'moment'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import { orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()

// ** Modal Header
const ModalHeader = props => {
  // ** Props
  const { children, store, handleTaskSidebar, setDeleted, deleted, important, setImportant, deleteTask, dispatch } =
    props


  // ** Function to delete task
  const handleDeleteTask = () => {
    setDeleted(!deleted)
    dispatch(deleteTask({ id: store.selectedTask.id, updatedBy: userId }))
    handleTaskSidebar()
  }

  const deletefn = () => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await handleDeleteTask()
        MySwal.fire({
          icon: 'success',
          title: 'Deleted',
          text: `To Do has been Deleted`,
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  return (
    <div className='modal-header d-flex align-items-center justify-content-between mb-1'>
      <h5 className='modal-title'>{children}</h5>
      <div className='todo-item-action d-flex align-items-center'>
        {store && !isObjEmpty(store.selectedTask) ? (
          <Trash className='cursor-pointer mt-25' size={16} onClick={() => deletefn()} />
        ) : null}
        {false &&
          <span className='todo-item-favorite cursor-pointer mx-75'>
            <Star
              size={16}
              onClick={() => setImportant(!important)}
              className={classnames({
                'text-warning': important === true
              })}
            />
          </span>
        }
        <X className='fw-normal ms-50 mt-25' size={16} onClick={handleTaskSidebar} />
      </div>
    </div>
  )
}

const TaskSidebar = props => {
  // ** Props
  const { open, handleTaskSidebar, store, dispatch, updateTask, selectTask, addTask, deleteTask } = props

  // ** States
  const [assignee, setAssignee] = useState([])
  const [assigneeArr, setAssigneeArr] = useState(null)
  const [tags, setTags] = useState('')
  const [desc, setDesc] = useState(EditorState.createEmpty())
  const [description, setDescription] = useState('')
  const [completed, setCompleted] = useState(false)
  const [important, setImportant] = useState(false)
  const [deleted, setDeleted] = useState(false)
  const [dueDate, setDueDate] = useState(new Date())

  const {
    control,
    setError,
    setValue,
    clearErrors,
    handleSubmit,
    formState: { errors }
  } = useForm({
    defaultValues: { title: '', dueDate: '', description: '', assignee: [] }
  })

  // ** Assignee Select Options
  const [assigneeOptions, setAssigneeOptions] = useState([])

  // ** Tag Select Options
  const tagOptions = [
    { value: 'team', label: 'Team' },
    { value: 'self', label: 'Self' }
  ]

  // ** Custom Assignee Component
  const AssigneeComponent = ({ data, ...props }) => {
    return (
      <components.Option {...props}>
        <div className='d-flex align-items-center'>
          <Avatar color='light-primary' initials content={data.name?.charAt(0)} className='d-block rounded-circle me-50' height='22' width='22' />
          <p className='mb-0'>{data.name}</p>
        </div>
      </components.Option>
    )
  }

  const getOrganizationUsers = async () => {
    axios.post('/organizationusers/dropdown').then(response => {
      const arr = response.data
      setAssigneeOptions(arr.organizationusers)
    }).catch((err) => {
      console.log(err)
    })

  }

  const completedToDo = async () => {
    setCompleted(true)
    const data = {
      updatedBy: userId,
      status: 2,
      id: store.selectedTask.id
    }
    await dispatch(updateStatus(data))

  }

  // ** Returns sidebar title
  const handleSidebarTitle = () => {
    if (store && !isObjEmpty(store.selectedTask)) {
      return (
        completed ? (<Button outline size='sm' color={'success'} onClick={(e) => e.preventDefault()}> Completed </Button>) : (<Button outline size='sm' onClick={() => completedToDo()} color={'secondary'} > Mark Completed </Button>)
      )
    } else {
      return 'Add To-Do'
    }
  }

  // ** Function to run when sidebar opens
  const handleSidebarOpened = () => {
    const { selectedTask } = store || {}
    if (!isObjEmpty(selectedTask)) {
      setValue('title', selectedTask.title)
      setAssignee(selectedTask.assigneeid)
      setAssigneeArr(assigneeOptions.filter((obj) => selectedTask.assigneeid.includes(obj.id)))
      setDueDate(moment(selectedTask.dueDate, 'x').format('MMM DD, Y'))
      const temp = EditorState.createWithContent(
        ContentState.createFromBlockArray(
          convertFromHTML(selectedTask.description)
        )
      )
      setCompleted(selectedTask.status === '2')
      setTags(tagOptions.find((obj) => obj.value === selectedTask.tags))
      setDesc(temp)
      setDescription(selectedTask.description)
    }
  }

  // ** Function to run when sidebar closes
  const handleSidebarClosed = () => {
    setTags(null)
    setDesc('')
    setValue('title', '')
    setAssignee([])
    setAssigneeArr(null)
    setCompleted(false)
    setImportant(false)
    setDueDate(new Date())
    dispatch(selectTask({}))
    clearErrors()
  }

  // ** Renders Footer Buttons
  const renderFooterButtons = () => {
    if (store && !isObjEmpty(store.selectedTask)) {
      return (
        <Fragment>
          <Button color='primary' className='update-btn update-todo-item me-1'>
            Update
          </Button>
          <Button color='secondary' onClick={handleTaskSidebar} outline>
            Cancel
          </Button>
        </Fragment>
      )
    } else {
      return (
        <Fragment>
          <Button color='primary' className='add-todo-item me-1'>
            Add
          </Button>
          <Button color='secondary' onClick={handleTaskSidebar} outline>
            Cancel
          </Button>
        </Fragment>
      )
    }
  }

  const handleChange = editorState => {
    setDescription(draftToHtml(convertToRaw(editorState.getCurrentContent())))
    setDesc(editorState)
  }

  const onSubmit = data => {

    const state = {
      dueDate: moment(dueDate).valueOf(),
      title: data.title,
      createdBy: userId,
      updatedBy: userId,
      tags: tags?.value ?? (assignee.length > 1 ? 'team' : 'self'),
      description: description.replace('/\r?\n|\r/g', ''),
      isCompleted: completed,
      isDeleted: deleted,
      isImportant: important,
      assignee
    }

    if (data.title.length) {
      if (isObjEmpty(errors)) {
        if (isObjEmpty(store.selectedTask) || (!isObjEmpty(store.selectedTask) && !store.selectedTask.title.length)) {
          dispatch(addTask(state))
        } else {
          dispatch(updateTask({ ...state, id: store.selectedTask.id }))
        }
        handleTaskSidebar()
        handleSidebarClosed()
      }
    } else {
      setError('title', {
        type: 'manual'
      })
    }
  }

  useEffect(() => {
    getOrganizationUsers()
  }, [])

  const handleAssigneeChange = (e) => {
    const tempArr = Array.isArray(e) ? e.map(x => x.id) : []
    tempArr.push(userId)
    setAssignee(tempArr)
    setAssigneeArr(e)
  }


  return (
    <Modal
      isOpen={open}
      toggle={handleTaskSidebar}
      className='sidebar-lg'
      contentClassName='p-0'
      onOpened={handleSidebarOpened}
      onClosed={handleSidebarClosed}
      modalClassName='modal-slide-in sidebar-todo-modal'
    >
      <Form id='form-modal-todo' className='todo-modal' onSubmit={handleSubmit(onSubmit)}>
        <ModalHeader
          store={store}
          deleted={deleted}
          dispatch={dispatch}
          important={important}
          deleteTask={deleteTask}
          setDeleted={setDeleted}
          setImportant={setImportant}
          handleTaskSidebar={handleTaskSidebar}
        >
          {handleSidebarTitle()}
        </ModalHeader>
        <ModalBody className='flex-grow-1 pb-sm-0 pb-3'>
          <div className='mb-1'>
            <Label className='form-label' for='task-title'>
              Title <span className='text-danger'>*</span>
            </Label>
            <Controller
              name='title'
              control={control}
              render={({ field }) => (
                <Input
                  id='task-title'
                  placeholder='Title'
                  className='new-todo-item-title'
                  invalid={errors.title && true}
                  {...field}
                />
              )}
            />
            {errors.title && <FormFeedback>Please enter a valid task title</FormFeedback>}
          </div>
          <div className='mb-1'>
            <Label className='form-label required' for='task-assignee'>
              Assignee
            </Label>
            <Select
              id='task-assignee'
              isMulti
              className='react-select'
              classNamePrefix='select'
              isClearable={false}
              options={assigneeOptions}
              theme={selectThemeColors}
              value={assigneeArr}
              onChange={handleAssigneeChange}
              components={{ Option: AssigneeComponent }}
              getOptionLabel={(option) => option.name}
              getOptionValue={(option) => option.id}
            />
          </div>
          <div className='mb-1'>
            <Label className='form-label required' for='due-date'>
              Due Date
            </Label>
            <Flatpickr
              id='due-date'
              name='due-date'
              className='form-control'
              onChange={(date, dateStr) => { setDueDate(dateStr); setValue('dueDate', dateStr) }}
              options={{ altInput: true, altFormat: "M j, Y", dateFormat: "M j, Y" }}
              value={dueDate}
            />
            {errors.dueDate && <FormFeedback>Please enter a valid Date</FormFeedback>}
          </div>
          <div className='mb-1'>
            <Label for='task-desc' className='form-label required'>
              Description
            </Label>
            <Editor
              editorState={desc}
              wrapperClassName='toolbar-bottom'
              toolbar={{
                options: ['inline', 'textAlign'],
                inline: {
                  inDropdown: false,
                  options: ['bold', 'italic', 'underline']
                }
              }}
              onEditorStateChange={handleChange}
            />
          </div>
          <div className='mb-1'>
            <Label className='form-label required' for='task-tags'>
              Tags
            </Label>
            <Select
              id='task-tags'
              className='react-select'
              classNamePrefix='select'
              isClearable={false}
              options={tagOptions}
              theme={selectThemeColors}
              value={tags}
              onChange={data => {
                setTags(data)
              }}
            />
          </div>
          <div>{renderFooterButtons()}</div>
        </ModalBody>
      </Form>
    </Modal>
  )
}

export default TaskSidebar
